@media (min-width: 680px) and (max-width: 1023px) {
  .wrap {
    overflow: hidden;
    width: 100%;
  }
  .container {
    width: 100%;
    padding: 0 5px;
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 780px;
  }
  .container::after {
    clear: both;
    content: '';
    display: block;
  }
  .container .container {
    width: 102%;
    width: calc(100% + 20px);
    margin-left: -10px;
  }
  .col12,
  .col11,
  .col10,
  .col9,
  .col8,
  .col7 {
    margin: 0;
